<template>
  <div class="order-list" :class="{ active: $store.state.isActive }">
    <div class="order-menu">
      <div class="title">
        <h1>訂單列表</h1>
        <button class="clear-btn" @click="$store.dispatch('CLEAR_ORDER_LIST')">清除</button>
      </div>
      <div class="order-title">
        <div class="th-order-id">編號</div>
        <div class="th-order-content">訂單內容</div>
        <div class="th-order-price">總價</div>
      </div>
      <div class="order-table">
        <div class="order" v-for="list of allList" :key="list.oId">
          <div class="order-id">
            <p>{{ list.oId + 1 }}</p>
          </div>
          <div class="order-content">
            <p v-for="(item, index) of list.order.order" :key="index">
              {{ item.name }}
              {{ item.num }}杯
            </p>
          </div>
          <div class="order-price">
            <p v-for="(item, index) of list" :key="index">{{ item.total }}</p>
            元
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'OrderList',
  data() {
    return {
      list: [],
    };
  },
  created() {
    // beforeunload事件在頁面重新整理時先觸發
    window.addEventListener('load', () => {
      this.$store.dispatch('READ_ORDER_LIST');
    });
    // 重新載入畫面時，呼叫READ_ORDER_LIST，把資料載回來，避免因為重新整理，而使資料無法正常顯示在畫面上
  },
  watch: {
    $route: {
      immediate: true,
      handler: function() {
        this.$store.dispatch('READ_ORDER_LIST');
      },
    },
  },
  computed: {
    allList() {
      return (this.list = this.$store.getters.finishList);
    },
    totalPrice() {},
  },
};
</script>

<style scoped>
.order-list {
  position: relative;
  left: 180px;
  top: 60px;
  width: calc(100% - 180px);
  transition: all 0.5s ease;
  display: flex;
  justify-content: center;
}

.order-list.active {
  width: calc(100% - 100px);
  left: 80px;
}

.order-menu {
  width: 1080px;
  background-color: #fff;
  margin: 50px 20px;
  border-radius: 10px;
  padding: 30px;
}

.order-menu .title {
  text-align: center;
  margin-bottom: 20px;
}

.order-menu .title .clear-btn {
  border: none;
  color: #fff;
  background-color: #e03e59;
  border-radius: 2px;
  cursor: pointer;
}

.order-menu .order-title {
  display: flex;
}
.order-menu .order-title div:first-child {
  width: 30%;
  text-align: center;
  font-size: 24px;
  margin-bottom: 15px;
}
.order-menu .order-title div:nth-child(2) {
  width: 50%;
  text-align: center;
  font-size: 24px;
  margin-bottom: 15px;
}
.order-menu .order-title div:last-child {
  width: 20%;
  text-align: center;
  font-size: 24px;
  margin-bottom: 15px;
}
.order-menu .order {
  display: flex;
}
.order .order-id {
  width: 30%;
  display: flex;
  justify-content: center;
  align-items: center;
  border-bottom: 1px solid #000;
}
.order .order-content {
  width: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  border-bottom: 1px solid #000;
}
.order .order-price {
  width: 20%;
  min-height: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  border-bottom: 1px solid #000;
}
.order .order-content p {
  width: 100%;
  text-align: center;
}
.order:nth-child(odd) {
  background-color: #b1ede8;
}
.order:nth-child(even) {
  background-color: #8eb8e5;
}
</style>
